<template>
  <v-card>
    <!-- <v-app id="inspire"> -->
    <v-data-table
      :headers="headers"
      :items.sync="desserts"
      :options.sync="options"
      :server-items-length="totalDesserts"
      :loading="loading"
      dense
    >
      <template v-slot:top>
        <v-toolbar flat display:fix>
          <v-toolbar-title>数据字典列表</v-toolbar-title>
          <v-divider class="mx-4" inset vertical></v-divider>
          <!-- <v-spacer></v-spacer> -->

          <v-tooltip bottom>
          <template v-slot:activator="{ on, attrs }">
            <v-icon v-on="on" v-bind="attrs" class="ml-4" @click="addDictInfo">mdi-plus-thick</v-icon>
          </template>
          <span>创建字典值</span>
        </v-tooltip>
          
          <!-- <v-btn color="primary" dark class="mb-2 ml-2" @click="addDictInfo">新建字典值</v-btn> -->
        </v-toolbar>
      </template>

      <template v-slot:item.enable="{ item }">
        <v-switch value :input-value="item.enable" @click.stop="updateType(item)"></v-switch>
      </template>

      <template v-slot:item.actions="{ item }">
        <!-- 编辑按钮 -->
        <v-tooltip bottom>
          <template v-slot:activator="{ on, attrs }">
            <v-icon
              class="mr-2"
              v-on="on"
              v-bind="attrs"
              @click="editDictInfo(item.id)"
            >mdi-pencil</v-icon>
          </template>
          <span>编辑</span>
        </v-tooltip>

        <!-- 删除按钮 -->
        <delete-hint icon="mdi-delete" :func="() => { return deleteDept(item.id) }"></delete-hint>
      </template>
    </v-data-table>

    <!-- 编辑或新增表单 -->
    <v-dialog v-model="dialog" max-width="290">
      <v-card>
        <v-card-title class="headline">{{dialogText}}</v-card-title>

        <v-card-text>
          <v-form ref="dictEditForm" v-model="valid" lazy-validation>
            <v-row>
              <v-col cols="12">
                <v-text-field
                  v-model="dictValue"
                  :rules="[v => !!v || '请填写字典值']"
                  label="字典值"
                  required
                ></v-text-field>
              </v-col>

              <v-col cols="12">
                <v-text-field
                  v-model="dictType"
                  :rules="[v => !!v || '请填写字典类型']"
                  label="字典类型"
                  required
                ></v-text-field>
              </v-col>
            </v-row>
          </v-form>
        </v-card-text>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="green darken-1" text @click="dialog = false, operation = ''">取消</v-btn>
          <v-btn color="green darken-1" text @click="editOrAdd">确定</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <!-- </v-app> -->
  </v-card>
</template>

<script>
export default {
  data() {
    return {
      expanded: [],
      search: "",
      singleExpand: false,
      options: {},
      totalDesserts: 0,
      loading: false,
      headers: [
        {
          text: "创建人",
          align: "start",
          value: "creator"
        },
        { text: "数据值", value: "value" },
        { text: "数据类型", value: "type" },
        { text: "创建时间", value: "createtime" },
        { text: "是否启用", value: "enable" },
        { text: "操作", value: "actions" }
      ],
      desserts: [],
      dialog: false,
      dialogText: "",
      operation: "",
      valid: true,
      dictValue: "",
      dictType: "",
      editId: 0
    };
  },

  created() {
    // this.getDictionartList();
  },

  watch: {
    options: {
      handler(val) {
        this.options = val;
        this.getDictionartList();
      },
      deep: true
    }
  },

  methods: {
    getDictionartList() {
      this.$http
        .post("/user/api/v1/dictionary/page", {
          pageNum: this.options.page,
          pageSize: this.options.itemsPerPage
        })
        .then(res => {
          this.desserts = res.data.records;
          this.totalDesserts = res.data.total;
        });
    },

    updateType(item) {
      // 点击后立刻改变状态，页面立即响应，如果请求失败，在将值改回旧值
      let oldEnable = item.enable;
      item.enable = !item.enable;
      this.$http
        .post("/user/api/v1/dictionary/updateType", {
          id: item.id,
          enable: !oldEnable
        })
        .then(res => {
          if (res.success) {
            // 无操作
          } else {
            item.enable = oldEnable;
            this.$store.commit("SET_SNACK", res);
          }
        })
        .catch(err => {
          item.enable = oldEnable;
          this.$store.commit("SET_SNACK", err);
        });
    },

    /**
     * 打开新建提示框
     */
    addDictInfo() {
      if (this.$refs.dictEditForm) {
        this.$refs.dictEditForm.resetValidation();
      }
      this.operation = "add";
      this.dictValue = "";
      this.dictType = "";
      this.dialog = true;
      this.dialogText = "新增";
    },

    /**
     * 打开编辑提示框
     */
    editDictInfo(id) {
      this.$http
        .get("/user/api/v1/dictionary/detailDictionary", {
          id: id
        })
        .then(res => {
          if (res.success) {
            this.dialogText = "编辑";
            this.dictValue = res.data.value;
            this.dictType = res.data.type;
            this.editId = id;
            this.dialog = true;
            this.operation = "edit";
          } else {
            this.$store.commit("SET_SNACK", res);
          }
        })
        .catch(err => this.$store.commit("SET_SNACK", err));
    },

    /**
     * 提交编辑或新增的内容
     */
    editOrAdd() {
      // operation的值为edit为编辑操作，否则为新增操作
      if (this.operation === "edit") {
        if (this.$refs.dictEditForm.validate()) {
          this.$http
            .post("/user/api/v1/dictionary/update", {
              id: this.editId,
              value: this.dictValue,
              type: this.dictType
            })
            .then(res => {
              if (res.success) {
                this.editId = 0;
                this.dialog = false
                // 重新渲染列表
                this.getDictionartList();                
              }
              this.$store.commit("SET_SNACK", res);
            })
            .catch(err => this.$store.commit("SET_SNACK", err));
        }
      } else {
        if (this.$refs.dictEditForm.validate()) {
          this.$http
            .post("/user/api/v1/dictionary/save", {
              value: this.dictValue,
              type: this.dictType
            })
            .then(res => {
              if (res.success) {
                this.dialog = false
                // 重新渲染列表
                this.getDictionartList();
              }
              this.$store.commit("SET_SNACK", res);
            })
            .catch(err => this.$store.commit("SET_SNACK", err));
        }
      }
      this.operation = ''
    },

    deleteDept(id) {
      this.$http
        .post("/user/api/v1/dictionary/deleteDictionary", {
          id: id
        })
        .then(res => {
          if (res.success) {
            this.getDictionartList();  
            this.$store.commit("SET_SNACK", res);
            return true;      
          }
          this.$store.commit("SET_SNACK", res);
        })
        .catch(err => this.$store.commit("SET_SNACK", err));
    }
  },

  components: {
    DeleteHint: () => import("./component/DeleteHint")
  }
};
</script>